.header {
  position: absolute;
  top: 40px;
  left: 0;
  width: 100%;

  &__wrapper {
    display: flex;
    align-items: center;
  }

  &__nav {
    margin: 0 0 0 auto;

    display: none;
  }

  &__logo{
    display: none;
  }

  &__burger {
    display: none;

    width: 42px;
    height: 25px;

    background: #FFFFFF;
    border-radius: 15px;
  }

  &-nav {
    &__list {
      display: flex;
      align-items: center;
    }

    &__item {
      &-m {
        display: none;
      }

      &-desc{
        display: block;
      }

      & + & {
        margin: 0 0 0 30px;
      }
    }

    &__link {
      font: 700 18px 'OfficinaSansC', sans-serif;
      text-transform: uppercase;
      color: #FFFFFF;
    }
  }

  &-burger {
    &:hover{
      cursor: pointer;
    }
    &__wrapper {

      width: 17px;
      height: 10px;

      position: relative;
    }

    &__item {
      width: 100%;
      height: 2px;

      background: #1B0C09;

      flex-shrink: 0;

      transition: 0.3s all linear;

      position: absolute;
      &:nth-child(1){
        top: 0;
      }
      &:nth-child(2){
        top: calc(50% - 1px);
      }
      &:nth-child(3){
        bottom: 0;
      }
    }

    &.active {
      .header {
        &-burger {
          &__item {
            position: absolute;

            &:nth-child(2) {
              left: 0;
              top: 4px;
              transform: rotate(45deg);
            }

            &:nth-child(1) {
              left: 0;
              top: 4px;
              transform: rotate(45deg);
            }

            &:nth-child(3) {
              right: 0;
              top: 4px;
              transform: rotate(-45deg);
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .header {
    top: 12px;

    &__wrapper {
      position: relative;
    }

    &__burger {
      display: none;
      align-items: center;
      justify-content: center;

      margin: 0 0 0 auto;

      z-index: 12;
      position: absolute;

      top: 0;
      right: 0;
    }

    &__logo {
      position: absolute;
      left: 0;
      top: 0;

      z-index: 12;

      display: block;

      &-img {
        width: 65px;
        height: 55px;

        object-fit: contain;
      }
    }

    &__nav {
      position: fixed;
      top: 0;
      left: 0;

      width: 100%;
      height: 100%;

      background: url("../img/nav-bg.jpg") center center/cover no-repeat;

      z-index: -1;
      opacity: 0;

      padding: 0 20px;

      transition: 0.3s all linear;

      &.active {

        opacity: 1;
        z-index: 10;

      }
    }


    &-nav {
      &__list {
        flex-direction: column;

        align-items: flex-start;

        padding: 155px 0 0 0;
      }

      &__item {
        display: none;

        & + & {
          margin: 16px 0 0 0;
        }

        &-m {
          display: block;
        }
      }

      &__link {
        font-weight: 900;
        font-size: 42px;
        line-height: 42px;

        background: linear-gradient(270deg, #E0AA23 0%, #FFF4C6 49.07%, #E7B72A 97.4%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;

        text-transform: capitalize;
      }
    }
  }
}