.footer {
  &__wrapper {
    padding: 30px 50px;

    position: relative;
    background: #fff;
  }

  &__block {
    max-width: 300px;
    width: 100%;

    &-big {
      max-width: 100%;
    }
  }

  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__copyright{
    display: none;

    margin: 8px 0 0 0;

    &-text{
      font:400 15px/18px 'OfficinaSansC', sans-serif;
      color: #933C06;
    }
  }

  &-logo {
    &__wrapper {
      height: 50px;
      width: 100%;

      display: flex;
      align-items: center;
    }

    &__img {
      height: 100%;

      object-fit: contain;
    }
  }

  &-links {
    &__list {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &__item {
      & + & {
        &::before {
          content: '|';

          color: #933C06;

          margin: 0 5px;
        }
      }
    }

    &__link {
      font: 400 14px/16px 'OfficinaSansC', sans-serif;

      color: #933C06;
      text-transform: uppercase;
    }
  }

  &-social {
    &__list {
      display: flex;
      align-items: center;
      justify-content: flex-end;

      &-item {
        & + & {
          margin: 0 0 0 15px;
        }
      }
    }

    &__item {
      display: flex;
      align-items: center;
    }

    &__icon {
      width: 15px;
      height: 15px;
      display: block;

      margin: 0 8px 0 0;

      img {
        width: 100%;
        height: 100%;

        object-fit: contain;
      }
    }

    &__label {
      font: 400 14px/16px 'OfficinaSansC', sans-serif;
      color: #933C06;
    }
  }
}

@media screen and (max-width: 1440px) {
  .footer {
    &__block {
      max-width: unset;
      width: unset;
    }
  }
}

@media screen and (max-width: 1280px) {
  .footer {
    &__wrapper {
      padding: 17px 16px 5px 16px;
    }

    &__content {
      flex-direction: column;
      align-items: flex-start;
    }

    &__block {
      &-big {
        margin: 26px 0 60px 0;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .footer{
    &-links{
      &__list{
        flex-direction: column;
        align-items: flex-start;
      }
      &__item{
        & + &{
          &::before{
            display: none;
          }
        }
      }
      &__link{
        font-size: 12px;
        line-height: 24px;
      }
    }
    &-social{
      &__list{
        flex-direction: column;
        align-items: flex-start;
        &-item{
          & + &{
            margin: 15px 0 0 0;
          }
        }
      }
      &__label{
        font-size: 15px;
        line-height: 18px;
      }
    }

    &__copyright{
      display: block;
    }
  }
}