*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: 'HelveticaNeue';
  font-weight: 400;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'OfficinaSansC';
  src: url("../fonts/OfficinaSansC/OfficinaSansBold/OfficinaSansBoldC.woff2") format("woff2"), url("../fonts/OfficinaSansC/OfficinaSansBold/OfficinaSansBoldC.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'OfficinaSansBlackC';
  src: url("../fonts/OfficinaSansC/OfficinaSansBlack/OfficinaSansBlackC.woff2") format("woff2"), url("../fonts/OfficinaSansC/OfficinaSansBlack/OfficinaSansBlackC.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'OfficinaSansC';
  src: url("../fonts/OfficinaSansC/OfficinaSansBook/OfficinaSansBookC.woff2") format("woff2"), url("../fonts/OfficinaSansC/OfficinaSansBook/OfficinaSansBookC-Book.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body.no-scroll {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin: 0 auto;
}

.home-page__wrapper {
  min-height: 100vh;
  background: url("../img/home-bg.jpg") top center/cover no-repeat;
}

.home-page__content {
  padding: 0 0 34px 0;
}

.home-banner__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.home-banner__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: bottom;
  min-height: 450px;
  image-rendering: -webkit-optimize-contrast;
}

.home-content__grid {
  display: grid;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  grid-auto-rows: 250px;
  gap: 25px;
}

.home-content__grid-item {
  grid-column-end: span 6;
}

.home-content__grid-item_size-big {
  grid-column-end: span 7;
}

.home-content__grid-item_size-small {
  grid-column-end: span 5;
}

.home-content__grid-item_size-full {
  grid-column-end: span 12;
}

.home-content__footer {
  margin: 50px 0 0 0;
}

.home-content__footer-wrapper {
  max-width: 1095px;
}

.home-content__footer-text {
  font: 400 14px/16px "OfficinaSansC", sans-serif;
  color: #CB973F;
}

.home-card {
  height: 100%;
}

.home-card__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  overflow: hidden;
  height: 100%;
}

.home-card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.to-top {
  display: none;
  position: absolute;
  z-index: 10;
  right: 16px;
  top: 16px;
}

.to-top:hover {
  cursor: pointer;
}

.to-top__wrapper {
  width: 25px;
  height: 25px;
}

.to-top__img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

@media screen and (max-width: 1140px) {
  .container {
    max-width: calc(100% - 32px);
  }
}

@media screen and (max-width: 991px) {
  .to-top {
    display: block;
  }
  .home-page__wrapper {
    background: url("../img/home-bg-m.jpg") top center/contain no-repeat #000;
  }
  .home-banner__img {
    min-height: 210px;
  }
  .home-content {
    transform: translateY(-15px);
  }
  .home-content__grid {
    grid-auto-rows: 350px;
  }
  .home-content__grid-item {
    grid-column-end: span 12;
  }
  .home-content__footer-wrapper {
    padding: 0 15px 0 20px;
  }
}

@media screen and (max-width: 991px) and (min-width: 700px) {
  .home-card__img_tablet-left {
    object-position: left;
  }
  .home-card__img_tablet-right {
    object-position: right;
  }
}

@media screen and (max-width: 700px) {
  .home-content__grid {
    grid-auto-rows: 350px;
  }
  .home-content__grid-item {
    grid-column-end: span 12;
  }
}

@media screen and (max-width: 600px) {
  .home-content__grid {
    grid-auto-rows: 250px;
  }
}

.header {
  position: absolute;
  top: 40px;
  left: 0;
  width: 100%;
}

.header__wrapper {
  display: flex;
  align-items: center;
}

.header__nav {
  margin: 0 0 0 auto;
  display: none;
}

.header__logo {
  display: none;
}

.header__burger {
  display: none;
  width: 42px;
  height: 25px;
  background: #FFFFFF;
  border-radius: 15px;
}

.header-nav__list {
  display: flex;
  align-items: center;
}

.header-nav__item-m {
  display: none;
}

.header-nav__item-desc {
  display: block;
}

.header-nav__item + .header-nav__item {
  margin: 0 0 0 30px;
}

.header-nav__link {
  font: 700 18px 'OfficinaSansC', sans-serif;
  text-transform: uppercase;
  color: #FFFFFF;
}

.header-burger:hover {
  cursor: pointer;
}

.header-burger__wrapper {
  width: 17px;
  height: 10px;
  position: relative;
}

.header-burger__item {
  width: 100%;
  height: 2px;
  background: #1B0C09;
  flex-shrink: 0;
  transition: 0.3s all linear;
  position: absolute;
}

.header-burger__item:nth-child(1) {
  top: 0;
}

.header-burger__item:nth-child(2) {
  top: calc(50% - 1px);
}

.header-burger__item:nth-child(3) {
  bottom: 0;
}

.header-burger.active .header-burger__item {
  position: absolute;
}

.header-burger.active .header-burger__item:nth-child(2) {
  left: 0;
  top: 4px;
  transform: rotate(45deg);
}

.header-burger.active .header-burger__item:nth-child(1) {
  left: 0;
  top: 4px;
  transform: rotate(45deg);
}

.header-burger.active .header-burger__item:nth-child(3) {
  right: 0;
  top: 4px;
  transform: rotate(-45deg);
}

@media screen and (max-width: 991px) {
  .header {
    top: 12px;
  }
  .header__wrapper {
    position: relative;
  }
  .header__burger {
    display: none;
    align-items: center;
    justify-content: center;
    margin: 0 0 0 auto;
    z-index: 12;
    position: absolute;
    top: 0;
    right: 0;
  }
  .header__logo {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 12;
    display: block;
  }
  .header__logo-img {
    width: 65px;
    height: 55px;
    object-fit: contain;
  }
  .header__nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url("../img/nav-bg.jpg") center center/cover no-repeat;
    z-index: -1;
    opacity: 0;
    padding: 0 20px;
    transition: 0.3s all linear;
  }
  .header__nav.active {
    opacity: 1;
    z-index: 10;
  }
  .header-nav__list {
    flex-direction: column;
    align-items: flex-start;
    padding: 155px 0 0 0;
  }
  .header-nav__item {
    display: none;
  }
  .header-nav__item + .header-nav__item {
    margin: 16px 0 0 0;
  }
  .header-nav__item-m {
    display: block;
  }
  .header-nav__link {
    font-weight: 900;
    font-size: 42px;
    line-height: 42px;
    background: linear-gradient(270deg, #E0AA23 0%, #FFF4C6 49.07%, #E7B72A 97.4%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    text-transform: capitalize;
  }
}

.footer__wrapper {
  padding: 30px 50px;
  position: relative;
  background: #fff;
}

.footer__block {
  max-width: 300px;
  width: 100%;
}

.footer__block-big {
  max-width: 100%;
}

.footer__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer__copyright {
  display: none;
  margin: 8px 0 0 0;
}

.footer__copyright-text {
  font: 400 15px/18px 'OfficinaSansC', sans-serif;
  color: #933C06;
}

.footer-logo__wrapper {
  height: 50px;
  width: 100%;
  display: flex;
  align-items: center;
}

.footer-logo__img {
  height: 100%;
  object-fit: contain;
}

.footer-links__list {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-links__item + .footer-links__item::before {
  content: '|';
  color: #933C06;
  margin: 0 5px;
}

.footer-links__link {
  font: 400 14px/16px 'OfficinaSansC', sans-serif;
  color: #933C06;
  text-transform: uppercase;
}

.footer-social__list {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.footer-social__list-item + .footer-social__list-item {
  margin: 0 0 0 15px;
}

.footer-social__item {
  display: flex;
  align-items: center;
}

.footer-social__icon {
  width: 15px;
  height: 15px;
  display: block;
  margin: 0 8px 0 0;
}

.footer-social__icon img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.footer-social__label {
  font: 400 14px/16px 'OfficinaSansC', sans-serif;
  color: #933C06;
}

@media screen and (max-width: 1440px) {
  .footer__block {
    max-width: unset;
    width: unset;
  }
}

@media screen and (max-width: 1280px) {
  .footer__wrapper {
    padding: 17px 16px 5px 16px;
  }
  .footer__content {
    flex-direction: column;
    align-items: flex-start;
  }
  .footer__block-big {
    margin: 26px 0 60px 0;
  }
}

@media screen and (max-width: 600px) {
  .footer-links__list {
    flex-direction: column;
    align-items: flex-start;
  }
  .footer-links__item + .footer-links__item::before {
    display: none;
  }
  .footer-links__link {
    font-size: 12px;
    line-height: 24px;
  }
  .footer-social__list {
    flex-direction: column;
    align-items: flex-start;
  }
  .footer-social__list-item + .footer-social__list-item {
    margin: 15px 0 0 0;
  }
  .footer-social__label {
    font-size: 15px;
    line-height: 18px;
  }
  .footer__copyright {
    display: block;
  }
}
