body {
  &.no-scroll {
    overflow: hidden;

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.container {
  max-width: 1140px;
  width: 100%;

  margin: 0 auto;
}

.home {
  &-page {
    &__wrapper {
      min-height: 100vh;

      background: url("../img/home-bg.jpg") top center/cover no-repeat;
    }

    &__content {
      padding: 0 0 34px 0;
    }
  }

  &-banner {
    &__wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &__img {
      width: 100%;
      height: 100%;

      object-fit: cover;
      object-position: bottom;

      min-height: 450px;

      image-rendering: -webkit-optimize-contrast;
    }
  }

  &-content {
    &__grid {
      display: grid;
      grid-template-columns: repeat(12, minmax(0, 1fr));
      grid-auto-rows: 250px;
      gap: 25px;

      &-item {
        grid-column-end: span 6;

        &_size {
          &-big {
            grid-column-end: span 7;
          }

          &-small {
            grid-column-end: span 5;
          }

          &-full {
            grid-column-end: span 12;
          }
        }
      }
    }

    &__footer {
      margin: 50px 0 0 0;

      &-wrapper {
        max-width: 1095px;
      }

      &-text {
        font: 400 14px/16px "OfficinaSansC", sans-serif;
        color: #CB973F;
      }
    }
  }

  &-card {
    height: 100%;

    &__wrapper {
      display: flex;
      align-items: center;
      justify-content: center;

      border-radius: 15px;
      overflow: hidden;

      height: 100%;
    }

    &__img {
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
  }
}

.to {
  &-top {
    display: none;

    position: absolute;
    z-index: 10;

    right: 16px;
    top: 16px;

    &:hover {
      cursor: pointer;
    }

    &__wrapper {
      width: 25px;
      height: 25px;
    }

    &__img {
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }
}

@media screen and (max-width: 1140px) {
  .container {
    max-width: calc(100% - 32px);
  }
}

@media screen and (max-width: 991px) {
  .to {
    &-top {
      display: block;
    }
  }
  .home {
    &-page {
      &__wrapper {
        background: url("../img/home-bg-m.jpg") top center/contain no-repeat #000;
      }
    }

    &-banner {
      &__img {
        min-height: 210px;
      }
    }

    &-content {
      transform: translateY(-15px);

      &__grid {
        grid-auto-rows: 350px;

        &-item {
          grid-column-end: span 12;
        }
      }

      &__footer {
        &-wrapper {
          padding: 0 15px 0 20px;
        }
      }
    }
  }
}

@media screen and (max-width: 991px) and (min-width: 700px) {
  .home {
    &-card {
      &__img {
        &_tablet {
          &-left {
            object-position: left;
          }

          &-right {
            object-position: right;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 700px) {
  .home {
    &-content {
      &__grid {
        grid-auto-rows: 350px;

        &-item {
          grid-column-end: span 12;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .home {
    &-content {
      &__grid {
        grid-auto-rows: 250px;
      }
    }
  }
}